import axios from 'axios';
import { Toast } from 'vant';
import config from "./config";
import { md5Key } from './../util/util.js';
let env = process.env.NODE_ENV;
let baseUrl;
if (env == 'production') {
    baseUrl = config.product;
} else {
    baseUrl = config.devolope;
}
var intent = axios.create({
    baseURL: baseUrl //服务器路径
})
intent.interceptors.request.use(function (config) {
    if (/get/i.test(config.method)) { //判断get请求
        config.params = config.params || {};
        config.params.t = Date.parse(new Date()) / 1000; //添加时间戳
    }
    //console.log(config, 'config');
    let { params } = config;
    let query = params;
    var queryStr;
    if (config.method == "get") {
        if (query) {
            queryStr = Object.getOwnPropertyNames(query).map(key => {
                return key + "=" + query[key];
            }).join("&");
            //console.log(queryStr, 'queryStr')
            // config.headers.authorition = md5Key(queryStr);
        }
    } else if (config.method == "post") {
        let data = config.data;
        data.openid = sessionStorage.getItem('openid');
        if (data && data.type != 'upload') {
            queryStr = JSON.stringify(data);
            //console.log(data, '请求参数')
            // config.headers.authorition = md5Key(queryStr);
        }
    }
    return config;
}, function (error) {
    // 对请求错误做些什么
    //console.log(error, 'error');
    return Promise.reject(error);
});
export const requestToken = function (data) {
    //console.log(data, 'data');
    return new Promise((resolve, reject) => {
        intent.get('/api/v1/hantai/weixin/getUserInfoByCode', { params: data }).then(res => {         
            resolve(res.data);
            //console.log('微信登录',res.data);
        }).catch(err => {
            //console.log('err',err);
            //console.log('微信登录错误',err);
            reject({ code: -1, msg: err })
        })
    })

}