
import { requestToken } from "./../api/requestToken.js";
import _config from "./../api/config";
import { wxconfig } from "./../api/wechat_config.js";

function getQueryVariable(variable) {
    var query = window.location.search.substring(1);
    var vars = query.split("&");
    for (var i = 0; i < vars.length; i++) {
        var pair = vars[i].split("=");
        if (pair[0] == variable) {
            return pair[1];
        }
    }
    return false;
}
window.isGettingCode = 0;

export const getCode = function () {
    function getQueryVariable(variable) {
        var query = window.location.search.substring(1);
        var vars = query.split("&");
        for (var i = 0; i < vars.length; i++) {
            var pair = vars[i].split("=");
            if (pair[0] == variable) { return pair[1]; }
        }
        return (null);
    }
    function getOpenIdByCode(code, resolve, reject) {
        if (code) {
            requestToken({ code: code, connect_redirect: 1, activityId: activityId }).then((res) => {
                // console.log(res, "请求token");
                if (res.data && res.data.openid) {
                    sessionStorage.setItem("openid", res.data.openid);
                    sessionStorage.setItem("userInfo", JSON.stringify(res.data));
                    // console.log("通过openid获取", res.data);
                    resolve({ openid: res.data.openid })
                } else {

                    // 参数顺序不能错,scope snsapi_userinfo 获取基本信息，无需关注公众号，用户手动授权
                    //snsapi_base 静默获取openid 必须关注公众号
                    //console.log("获取openId 失败")
                    let url = location.href;
                    //console.log('本页路径', url, "code", code);
                    url = encodeURI(url);
                    //console.log(wxconfig.redict_url, 'wxconfig.redict_url')
                    // location.href =
                    //     "https://open.weixin.qq.com/connect/oauth2/authorize?appid=" +
                    //     wxconfig.appid +
                    //     "&redirect_uri=" +
                    //     url +
                    //     "&response_type=code&scope=snsapi_userinfo&state=STATE#wechat_redirect&connect_redirect=1";
                    reject({ openid: '' });
                }
            }).catch(err => {
                //console.log(err, 'rerr')
            });



        }
    }

    return new Promise((resolve, reject) => {
        var openid = sessionStorage.getItem("openid");
        var code = getQueryVariable("code");
        //跳转之前存起来
        let activityId = getQueryVariable('activityId') || sessionStorage.getItem('activityId');
        //console.log('获取到code', code);

        if (_config.env != 'prd') {
            // getOpenIdByCode(code, resolve, reject);
            resolve({ openid: "" })
            return;
        } else {
            if (openid && openid != "null") {
                resolve({ openid: openid })
            } else {
                if (code) {
                    window.isGettingCode = 1;
                    if (activityId) {
                        requestToken({ code: code, connect_redirect: 1, activityId: activityId }).then((res) => {
                            //console.log(res, "请求token");
                            if (res.data && res.data.openid) {
                                sessionStorage.setItem("openid", res.data.openid);
                                sessionStorage.setItem("userInfo", JSON.stringify(res.data));
                                //console.log("通过openid获取", res.data.openid);
                                resolve({ openid: res.data.openid })
                            } else {
                                // 参数顺序不能错,scope snsapi_userinfo 获取基本信息，无需关注公众号，用户手动授权
                                //snsapi_base 静默获取openid 必须关注公众号
                                //console.log("获取openId 失败")
                                let url = location.href;
                                //console.log('本页路径', url, "code", code);
                                url = encodeURI(url);
                                //console.log(wxconfig.redict_url, 'wxconfig.redict_url')
                                // location.href =
                                //     "https://open.weixin.qq.com/connect/oauth2/authorize?appid=" +
                                //     wxconfig.appid +
                                //     "&redirect_uri=" +
                                //     url +
                                //     "&response_type=code&scope=snsapi_userinfo&state=STATE#wechat_redirect&connect_redirect=1";
                                reject({ openid: '' });
                            }
                        }).catch(err => {
                            console.log(err, 'rerr')
                        });
                    } else {
                        console.log('活动id为空')
                        // alert('活动id 不能为空')
                    }




                } else {
                    let url = location.href;
                    //console.log('activityId 获取到活动id', getQueryVariable('activityId'));
                    if (getQueryVariable('activityId')) {
                        // 保存activityId 不需要可以去掉
                        sessionStorage.setItem('activityId', getQueryVariable('activityId'));
                    }
                    url = encodeURI(url);
                    var href = "https://open.weixin.qq.com/connect/oauth2/authorize?appid=" +
                        wxconfig.appid + "&redirect_uri=" +
                        url +
                        "&response_type=code&scope=snsapi_userinfo&state=STATE#wechat_redirect&connect_redirect=1";
                    reject({ openid: '' });
                    //console.log(href);


                    //console.log(url);
                    // 参数顺序不能错,scope snsapi_userinfo 获取基本信息，无需关注公众号，用户手动授权
                    //snsapi_base 静默获取openid 必须关注公众号
                    location.href =
                        "https://open.weixin.qq.com/connect/oauth2/authorize?appid=" +
                        wxconfig.appid + "&redirect_uri=" +
                        url +
                        "&response_type=code&scope=snsapi_userinfo&state=STATE#wechat_redirect&connect_redirect=1";


                }
            }
        }


    });
}
