import axios from 'axios';
import router from './../router/index';
import config from "./config";
import { getCode } from "./wechat.js"
let env = process.env.NODE_ENV;
const requstingUrls = [];//正在请求中的路径
let baseUrl;
if (env == 'production') {
    baseUrl = config.product;
} else {
    baseUrl = config.devolope;
}
var instance = axios.create({
    baseURL: baseUrl
});

// 添加请求拦截器
instance.interceptors.request.use(async function (config) {
    
    let s_data = await getCode();
    //console.log('getCode', s_data);
    //console.log('当前链接为', location.href, "请求链接为");
    if (/get/i.test(config.method)) { //判断get请求
        config.params = config.params || {};
        config.params.t = Date.parse(new Date()) / 1000; //添加时间戳
        // 添加oenid 测试去掉
        config.params.openid = s_data.openid || sessionStorage.getItem('openid');
        //console.log("openid request.js get", s_data.openid, sessionStorage.getItem('openid'));
        if (!sessionStorage.getItem('openid')) {
            //console.log('未获取到openId code 当前链接', location.href);
        } else {
            //console.log('获取到openid 当前链接', location.href);
        }
    }
    //console.log(config, 'config request.js');
    let { params } = config;
    let query = params;
    var queryStr;
    if (config.method == "get") {
        if (query) {
            queryStr = Object.getOwnPropertyNames(query).map(key => {
                return key + "=" + query[key];
            }).join("&");
            //console.log(queryStr, 'queryStr')
            // config.headers.authorition = md5Key(queryStr);
        }

    } else if (config.method == "post") {
        let data = config.data;
        //console.log("openid request.js post", s_data.openid, sessionStorage.getItem('openid'));
        data.openid = s_data.openid || sessionStorage.getItem('openid') ;
        if (data && data.type != 'upload') {
            queryStr = JSON.stringify(data);
            //console.log(data, '请求参数')
            // config.headers.authorition = md5Key(queryStr);
        }
    }
    return config;
}, function (error) {
    // 对请求错误做些什么
    //console.log(error, 'error');
    return Promise.reject(error);
});

// 添加响应拦截器
instance.interceptors.response.use(function (response) {
    // Toast.clear();
    //console.log(response, "请求响应");
    let url = response.config.url;
    let index = requstingUrls.indexOf(url);
    if (index > -1) {
        requstingUrls.splice(index - 1, 1);
    }
    // 对响应数据做点什
    if (response.status == 200) {
        return Promise.resolve(response.data)
    } else {
        // Toast('网络错误');
        return Promise.reject({ msg: "网络错误" });
    }

}, function (error) {
    // 对响应错误做点什么
    // alert(JSON.stringify(error));
    // Toast.clear();
    //console.log('请求错误2' + error)


    return Promise.reject(error);
});

let request = {
    // store: store,
    get: function (url, data) {
        return instance.get(url, { params: data })
    },
    $get: function (url, data, callBack) { //带回调  
        return instance.get(url, { params: data }).then(res => {
            if (callBack) {
                callBack(res);
            }
        });
    },
    $post: function (url, data, callBack) {

        instance.post(url, data).then(res => {
            if (callBack) {
                callBack(res);
            }
        });


    },
    post: function (url, data, callBack) {
        return instance.post(url, data);
    }
}

export default request;
