<template>
  <div class="flexfooter">
    <audio
      controls
      style="visibility: hidden"
      id="audio"
      :autoplay="true"
      src="http://st.chaoyindj.com//ywcs/Mp3%20Dj%E5%B0%8F%E9%BB%91-%E5%85%A8%E8%8B%B1%E6%96%87House%E9%9F%B3%E4%B9%90%E5%95%86%E4%B8%9A%E5%9C%9F%E5%97%A8%E5%8C%85%E6%88%BF%E6%B8%85%E9%A3%8E%E7%8B%AC%E5%AE%B6%E6%85%A2%E6%91%87%E4%B8%B2%E7%83%A7.mp4"
    ></audio>
    <div class="footer">
      <!-- 背景-->
      <a class="footer" href="javascript:void(0)" rel="noopener noreferrer">
        <div class="bg">
          <div class="list">
            <div class="list_left">
              <div
                class="left_item"
                v-for="item in list"
                :key="item.funcNumber"
                @click="
                  item.funcNumber == '001'
                    ? showText()
                    : item.funcNumber == '002'
                    ? nav('messagetowall')
                    : item.funcNumber == '003'
                    ? nav('shake')
                    : item.funcNumber == '004'
                    ? nav('messagetowall')
                    : item.funcNumber == '005'
                    ? nav('awardRecord')
                    : ''
                "
              >
                <div class="list_item" :style="{ backgroundImage: item.funcIcon }">
                  <img :src="item.funcIcon" alt="" class="funcIcon" />
                </div>
                <div class="text">
                  {{
                    item.funcName 
                  }}
                </div>
              </div>
            </div>
          </div>
        </div>
      </a>
    </div>
  </div>
</template>

<script>
import { inject, reactive, provide, ref, toRefs, onMounted } from "vue";
import { useRoute, useRouter } from "vue-router";
import request from "./../api/request.js";
// @ is an alias to /src

export default {
  name: "footer",
  components: {},
  emits: ["showText"],
  props: ["showFooter"],
  setup(props, context) {
    console.log(props, "props");
    let route = useRoute();
    let state = reactive({ list: [], activityId: 1, music: "" });
    
    let getList = async function () {
      let data = await request.get("/api/v1/activity/selectActivityFunction", {
        activityId: route.query.activityId,
      });
      state.list = data.data || [{}, {}, {}];
    };
    let showText = function () {
      console.log(123);
      context.emit("showText");
    };
    getList();
    let router = useRouter();
    let nav = (name) => {
      router.push({ name: name, query: route.query });
    };
    onMounted(() => {});
    return {
      nav,
      showText,
      ...toRefs(state),
    };
  },
  methods: {
    // showText: function () {
    //   console.log("234");
    // },
  },
};
</script>
<style lang="scss">
.footer {
  width: 100%;
  height: 148px;
  z-index: 99;
  position: fixed;
  bottom: 0;
}
.bg {
  width: 100%;
  position: absolute;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  transition: height 0.5s;
  -moz-transition: height 0.5s; /* Firefox 4 */
  -webkit-transition: height 0.5s; /* Safari 和 Chrome */
  -o-transition: height 0.5s;
  height: 148px;
}
.list {
  display: flex;
  justify-content: space-between;
}
.list_left {
  display: flex;
  font-size: 18px;
  font-family: Source Han Sans SC;
  font-weight: 400;
  color: #ffffff;
  width: 100%;
  justify-content: space-around;
}
.funcIcon {
  width: 85px;
  height: 85px;
  border-radius: 50%;
}
.list_item {
  width: 85px;
  height: 85px;
  border-radius: 50%;
  background: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 5px;
}
.left_item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 16px;
  padding-bottom: 16px;
  width: 120px;
}
</style>
