import { createRouter, createWebHistory } from 'vue-router'
import Index from "./../views/index.vue";
const routes = [
  {
    path: '/',
    name: 'index',
    component: Index,
    props: {
      title: "首页"
    }
  },
  {
    path: "/messagetowall",
    name: "messagetowall",
    component: () => import("./../views/MessageToWall.vue"),
    props: {
      title: "消息上墙"
    }
  }, {
    path: "/awardrecord",
    name: "awardRecord",
    props: {
      title: "中奖记录"
    },
    component: () => import("./../views/awardRecord.vue")
  },
  {
    path: "/shake",
    name: "shake",
    component: () => import("./../views/shake.vue"),
    props: {
      title: "摇一摇"
    },
  }

]

const router = createRouter({
  history: createWebHistory("/wechat/"),
  // history: createWebHistory("/hips/wx/"),
  routes
});

// 设置标题
router.beforeEach((to, from, next) => {
  console.log('beforeEach');
  if (to.matched[0]&&to.matched[0].props.default.title) {
    document.title = to.matched[0].props.default.title
  }
  next()
})
export default router
