<template>
  <div class="main">
    <div class="shake">
      <div class="header_div">
        <div class="avatar">
          <img class="avatar_img" :src="userInfo.headImg" />
          <div class="nickName">{{ userInfo.nickName }}</div>
        </div>
      </div>
      <div class="mid_img_icons">
        <img class="centerTextImg" src="./../assets/images/centerText.png" alt="" />
      </div>
      <div class="number_div">
        <div class="welText">欢迎来到 瀚泰现场互动平台</div>
        <div class="welText_number" v-show="showSort">
          您是第{{ userInfo.signSort }}位签到
        </div>
      </div>
    </div>
    <appfooter @showText="showText"></appfooter>
  </div>
</template>

<script>
import { reactive, toRefs } from "vue";
// @ is an alias to /src
import appfooter from "./../components/footer.vue";
import request from "./../api/request";
import { log } from "three";
import { useRoute } from "vue-router";
export default {
  name: "Home",
  components: {
    appfooter,
  },
  setup(props, context) {
    let route =useRoute();
    const state = reactive({
      userInfo: {},
      showSort: true, //展示名次
      activityId:route.query.activityId
    });
    if (route.query.activityId) {
      sessionStorage.setItem("activityId", route.query.activityId);
      state.activityId=route.query.activityId;
    }
    async function getUserInfo() {
      let data = {
        activityId: state.activityId,
      };
      console.log("data", data);
      let resp = await request.get("/api/v1/user/sign/findSignListByOpenid", data);
      if (resp.code == 0) {
        state.userInfo = resp.data;
        sessionStorage.setItem("userInfo", JSON.stringify(state.userInfo));
      }
    }
    async function sign() {
      let data = {
        activityId:state.activityId,
      };
      let resp = await request.get("/api/v1/user/sign/sign", data);
      if (resp.code == 0) {
        state.userInfo = resp.data;
        sessionStorage.setItem("userInfo", JSON.stringify(state.userInfo));
      }
    }
    getUserInfo();
    sign();
    return {
      ...toRefs(state),
    };
  },
  methods: {
    showText: function (event) {
      console.log("showText");
      this.showSort = !this.showSort;
    },
  },
};
</script>
