<template>
  <div class="outer">
    <!-- vue3 写法 -->
    <router-view v-slot="{ Component }">
      <keep-alive>
        <component :is="Component" />
      </keep-alive>
    </router-view>
  </div>
</template>
<script>
import { provide, reactive, ref } from "vue";
import config from "./api/config.js";
import request from "./api/request";
export default {
  components: {
    // appfooter,
  },
  setup(props) {
    if (config.env == "dev") {
      // sessionStorage.setItem("openid", "oBI0B6YYisd6q7O_9bVR9fYYnhXQ");
      debugger
      sessionStorage.setItem("openid", "oBI0B6bR0vMoIqrlEdCLMStMLQok"); //建东
    }
  },
  methods: {},
  mounted() {
    var activityId;
    if (this && this.$route.query.activityId) {
      activityId = this.$route.query.activityId;
    }
    async function sign() {
      // 签到
      let data = {
        activityId: activityId,
      };
      ;

      let resp = await request.get("/api/v1/user/sign/sign", data);
      if (resp.code == 0) {
        sessionStorage.setItem("userInfo", JSON.stringify(resp.data));
      }
    }
    if (activityId) {
      sign();
    }
  },
};
</script>
